          title="QU PASA DESPUS DEL PRIMER MES?"
          description="Nuestro objetivo tambin es que las personas nos conozcan para hacer de este espacio un mercado digital en el cual cualquiera pueda encontrarte. Para esto requerimos de tu apoyo,  por eso y pensando en tu bienestar, hemos desarrollado los siguientes planes los cuales puedes pagar mensual sin ninguna clusula de permanencia. Es decir, puedes cancelar en cualquier momento."
